.product{
    display:flex;
    flex-direction: column;
    margin:10px;
    width: 200px;
    overflow: hidden;

}
.rightproduct{
    display: flex;
    flex-direction: column;

}
.rightproduct>div{
    font-size: 1.1rem;
    font-weight: 400;
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}
.leftproduct{
    height: 240px;
    width: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}
.productimage{
    height: 220px;
    width: 180px;
}
.productimage:hover{
    height: 230px;
    width: 190px;
}
.middleproduct{
    font-size: 2rem;
    cursor: pointer;
   
}
.prodname{
    font-size: 1.1rem;
}
.prodname:hover{
    text-decoration: underline;
}


/* Responsive design --------------------------------------------------------------------------------------->*/



@media (width<800px) {
    .product{
        width: 100px;
    }
    .leftproduct{
        height: 100px;
        width: 100px;
    }
    .productimage{
        height: 100px;
        width: 100px;
    }
    .productimage:hover{
        height: 100px;
        width: 100px;
    }
    .middleproduct{
        font-size: 1rem;
       
    }
}